"use client";
import { useRouter } from "next/navigation";
import React, { useState } from "react";

const Signup = () => {
  const router = useRouter();

  // Add confirm password if you want validation
  const [name, setName] = useState(""); 
  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");
  const [confirmPassword, setConfirmPassword] = useState("");
  const [status, setStatus] = useState("");
  const [loading, setLoading] = useState(false);

  const handleSignup = async (e) => {
    e.preventDefault();
    setStatus("");

    // Basic client-side validation for password match
    if (password !== confirmPassword) {
      setStatus("Passwords do not match");
      return;
    }

    setLoading(true);

    try {
      const res = await fetch("/api/signup", {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify({ name, email, password }),
      });

      const data = await res.json();

      if (res.ok) {
        setStatus(data.message || "Signup successful!");
        // Optionally redirect to login page after signup
        setTimeout(() => {
          router.push("/login");
        }, 1500);
      } else {
        setStatus(data.error || "Signup failed.");
      }
    } catch (error) {
      setStatus("Server error. Try again.");
    } finally {
      setLoading(false);
    }
  };

  return (
    <section className="px-6 py-10 flex justify-center items-center min-h-screen bg-white">
      <div className="bg-green-50 shadow-lg rounded-lg p-8 w-full max-w-md min-h-[600px]">
        <h1 className="text-3xl font-bold mb-6 text-center">Signup</h1>
        <form onSubmit={handleSignup} className="space-y-6">
          <input
            type="text"
            placeholder="Name"
            value={name}
            onChange={(e) => setName(e.target.value)}
            required
            className="w-full border border-gray-300 p-3 rounded focus:outline-none focus:ring-2 focus:ring-green-500"
          />
          <input
            type="email"
            placeholder="Email"
            value={email}
            onChange={(e) => setEmail(e.target.value)}
            required
            className="w-full border border-gray-300 p-3 rounded focus:outline-none focus:ring-2 focus:ring-green-500"
          />
          <input
            type="password"
            placeholder="Password"
            value={password}
            onChange={(e) => setPassword(e.target.value)}
            required
            className="w-full border border-gray-300 p-3 rounded focus:outline-none focus:ring-2 focus:ring-green-500"
          />
          <input
            type="password"
            placeholder="Confirm Password"
            value={confirmPassword}
            onChange={(e) => setConfirmPassword(e.target.value)}
            required
            className="w-full border border-gray-300 p-3 rounded focus:outline-none focus:ring-2 focus:ring-green-500"
          />
          <button
            type="submit"
            disabled={loading}
            className="bg-green-600 text-white px-6 py-3 w-full rounded hover:bg-green-800 transition disabled:opacity-50"
          >
            {loading ? "Signing up..." : "Signup"}
          </button>
          <div>
            Already have an account?{" "}
            <button
              type="button"
              onClick={() => router.push("/login")}
              className="text-lg text-gray-900 font-bold"
            >
              Login
            </button>
          </div>
        </form>
        {status && <p className="mt-4 text-center text-red-600">{status}</p>}
      </div>
    </section>
  );
};

export default Signup;
